export const validateDateMask = (dateString) => {
  const regex =
    /^(0[1-9]|[12][0-9]|3[01])[- /.](0[1-9]|1[012])[- /.](19|20)\d\d$/;
  if (!regex.test(dateString)) {
    return false;
  }
  const dateObj = new Date(dateString);
  return date.isValid(dateObj);
};

export const calcularFecha = (f1, f2) => {
  // Create Date objects for the two dates
  const date1 = new Date(f1); // Quasar Asset management date
  const date2 = new Date(f2); // VBI appointment date

  // Calculate the difference in milliseconds
  const diffInMilliseconds = date2 - date1;

  // Convert milliseconds to days (1000 ms * 60 s * 60 min * 24 hours)
  const diffInDays = diffInMilliseconds / (1000 * 60 * 60 * 24);

  // Round the result to avoid small decimal numbers
  const daysBetweenDates = Math.round(diffInDays);

  return daysBetweenDates;
};

