export const filterFnActividad = (val, update, selectParroquia) => {
  const vector = ref([]);
  update(() => {
    const needle = val.toLowerCase();

    vector.value = selectParroquia.filter(
      (v) => v.label.toLowerCase().indexOf(needle) > -1
    );
  });
  return vector;
};

